.overview-page {
  /*  */
}

.title {
  margin-bottom: 20px;
}

.wrapFilter {
  margin-bottom: 27px;
}

.filter {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  position: relative;
  margin-bottom: 11px;
  max-width: 350px;
  width: 100%;
  height: 37px;
}

.search {
  width: 100%;
  height: 100%;
  padding: 11px;
  background: #282828;
  box-sizing: border-box;
  border: 1px solid #acacac;
  border-radius: 8px;
  color: #fff;
}

.copy {
  position: absolute;
  top: 50%;
  right: 11px;
  transform: translateY(-50%);
}

.noUnlockRefCode {
  color: #FF7171;
}

.linkMission {
  text-decoration: underline;
}

.linkMission:hover {
  color: #FF7171;
  text-decoration: underline;
}