.smt_form {
  max-width: 456px;
  width: 100%;
  padding: 34.5px 37px;
  box-sizing: border-box;
  background: #282828;
  border-radius: 8px;
}

.smt_titleForm {
  font-weight: 700;
  font-size: 28px;
  line-height: 34px;
  color: #FFFFFF;
  margin-bottom: 6px;
}

.smt_descriptionForm {
  color: rgba(255, 255, 255, 0.4);
  margin-bottom: 32px;
}

.smt_rememberMe {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
}

.smt_buttonSubmit,
.smt_buttonSubmit:hover {
  margin-top: 40px;
  width: 100%;
  box-sizing: border-box;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 15.5px 0px;
  background: #1EBD23;
  border-radius: 12px;
  border: 0;
  font-weight: 600;
  font-size: 14px;
  line-height: 17px;
  color: #FFFFFF;
  cursor: pointer;
}

.smt_moreOption {
  display: flex !important;
  align-items: center !important;
  justify-content: center !important;
  color: rgba(255, 255, 255, 0.5) !important;
  font-size: 14px !important;
  width: 100% !important;
  margin-bottom: 0px !important;
  white-space: nowrap !important;
}

.smt_moreOption_link {
  font-weight: 600;
  color: #1EBD23;
}

.smt_resendCode {
  font-size: 16px;
  line-height: 15px;
  color: #FFF066;
  text-align: center;
  cursor: pointer;
}

.resend {
  display: flex;
  align-items: center;
  justify-content: center;
}

@keyframes rotate {
  100% {
    rotate: 360deg;
  }
}

.loading {
  width: 40px;
  height: 40px;
  border-top: 2px solid #1EBD23;
  border-right: 2px solid #1EBD23;
  border-bottom: 2px solid#1EBD23;
  border-left: 2px solid transparent;
  border-radius: 50%;
  margin: auto;
  animation: rotate 1s linear infinite;
}

@media (max-width: 479px) {
  .smt_form {
    background: transparent;
    padding: 34.5px 16px;
  }
}

@media (max-width: 350px) {
  .smt_moreOption {
    display: block !important;
    text-align: center;
  }
}