.overview-page {
  /*  */
}

.title {
  margin-bottom: 20px;
}

.statistical {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  gap: 28px;
}

.statisticalItem {
  width: 202px;
  background: #E3F5FF;
  border-radius: 16px;
  padding: 24px;
  display: flex;
  align-items: center;
  position: relative;
}

.statisticalItem_type {
  margin-bottom: 12px;
}

.statistical_info {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 14px;
  width: 100%;
}

.statistical_trend {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 5.5px;
}

.wrapperStatistical_info {
  width: 100%;
}

.statistical_icon {
  width: 30px;
  height: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 100%;
  background: #1C1C1C;
  position: absolute;
  top: 8px;
  right: 8px;
}

.statistical_icon svg {
  width: 20px;
  height: 20px;
}

.spaceLine {
  height: 96px;
  width: 100%;
}

@media (max-width: 767px) {
  .statistical {
    flex-wrap: wrap;
  }

  .statisticalItem {
    width: calc(50% - 14px);
  }
}

@media (max-width: 600px) {
  .statistical {
    flex-wrap: wrap;
    gap: 14px;
  }

  .statisticalItem {
    width: 100%;
  }
}