.wrapper {
  width: 100%;
  height: 69px;
  padding: 0px 28px;
  box-sizing: border-box;
  position: fixed;
  top: 0;
  left: 0;
  background: transparent;
}

.logoDesktop {
  display: flex;
  align-items: center;
  height: 100%;
}

.logoMobile {
  display: none;
}

@media (max-width: 479px) {
  .wrapper {
    background: #1C1C1C;
    box-shadow: 4px 4px 12px rgba(35, 129, 3, 0.24);
  }

  .logoDesktop {
    display: none;
  }

  .logoMobile {
    display: flex;
    align-items: center;
    height: 100%;
  }
}