body {
    padding: 0;
    margin: 0;
    font-family: Ubuntu, sans-serif;
    -webkit-font-smoothing: antialiased;
    background: #1C1C1C;
}

* {
    box-sizing: border-box;
    transition: all .3s linear;
    font-family: Ubuntu, sans-serif;
    -webkit-font-smoothing: antialiased;
}

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
    margin: 0px;
    padding: 0px;
    border: 0px;
    font-size: 16px;
    font-weight: 400;
    color: #fff;
    vertical-align: baseline;
}

a {
    color: inherit;
    text-decoration: none;
}

#root {
    overflow: hidden;
}

* {
    font-family: Ubuntu;
}

body::-webkit-scrollbar,
.no-sb::-webkit-scrollbar {
    height: 0;
    width: 0;
    display: none;
    background-color: transparent;
}

.walletconnect-modal__base {
    max-width: 300px !important;
}

.Toastify__toast-body * {
    color: #000;
    text-transform: capitalize;
}

.no-scroll {
    overflow: hidden;
}

/* FORM */

.wrap-form .ant-form-item-label,
.wrap-form .ant-form-item-control {
    min-width: 100%;
    text-align: start;
    margin-left: 0;
}

.wrap-form .ant-form-item {
    margin-bottom: 5px;
}

.wrap-form .ant-form-item-row {
    flex-wrap: wrap;
}

