/* ---------------- */
.color-black {
    color: black;
}

.color-white {
    color: white;
}

.width-scroll::-webkit-scrollbar-track {
    -webkit-box-shadow: none;
    background-color: transparent;
    border-radius: 10px;
}

.width-scroll::-webkit-scrollbar {
    height: 6px;
    background-color: transparent;
}

.width-scroll::-webkit-scrollbar-thumb {
    background-color: #D9D9D9;
    border-radius: 10px;
}