.wrapper {
  width: 100%;
  height: 100%;
}

.showPopup {
  position: relative;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.buttonShow {
  background: #059F0A;
  border-radius: 8px;
  width: 186px;
  height: 32px;
  border: 0;
  font-weight: 400;
  font-size: 14px;
  color: #FFFFFF;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
}

.smt_form {
  max-width: 456px;
  width: 100%;
  padding: 34.5px 37px;
  box-sizing: border-box;
  background: #282828;
  border-radius: 8px;
}

.smt_titleForm {
  font-weight: 700;
  font-size: 28px;
  line-height: 34px;
  color: #FFFFFF;
  margin-bottom: 6px;
}

.smt_descriptionForm {
  color: rgba(255, 255, 255, 0.4);
  margin-bottom: 32px;
}

.smt_buttonSubmit,
.smt_buttonSubmit:hover {
  width: 100%;
  box-sizing: border-box;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 15.5px 0px;
  background: #1EBD23;
  border-radius: 12px;
  border: 0;
  font-weight: 600;
  font-size: 14px;
  line-height: 17px;
  color: #FFFFFF;
  cursor: pointer;
}

@media (max-width: 479px) {
  .smt_form {
    background: transparent;
    padding: 34.5px 16px;
  }

  .showPopup {
    align-items: flex-start;
  }
}