.overview-page {
  /*  */
}

.title {
  margin-bottom: 20px;
}

.sort {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  margin-bottom: 11px;
}


.wrapperPopup {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
}

.popup {
  max-width: 360px;
  min-height: 213px;
  width: 100%;
  background: #282828;
  border-radius: 8px;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 8px;
  box-sizing: border-box;
}

.close {
  position: absolute;
  top: 8px;
  right: 8px;
  cursor: pointer;
}

.container {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
}

.content {
  text-align: center;
}

.amount {
  font-weight: 700;
  font-size: 20px;
  line-height: 24px;
  color: #FFFFFF;
  margin: 7px auto 10px;
}

.description {
  color: rgba(255, 255, 255, 0.4);
}

.submit {
  max-width: 186px;
  width: 100%;
  height: 32px;
  background: #059F0A;
  border-radius: 8px;
  border: 0px;
  font-size: 14px;
  color: #FFFFFF;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: auto;
  margin-top: 20px;
}